html {
  --color-black: #0D0D0D;
  --color-white: #fff;
  --color-white-50 : rgba(255,255,255,.5);
  --color-white-30 : rgba(255,255,255,.3);
  --color-white-20 : rgba(255,255,255,.2);
  --color-white-10 : rgba(255,255,255,.1);
  --color-white-5 : rgba(255,255,255,.05);
  --color-primary: #5E90F2;
  --color-hover: #2460da;
  --color-highlight: #F24B59;
  --color-area: #2c2c2c;
}

body {
  background-color: var(--color-black);
  font-family: "articulat-cf", sans-serif;
  line-height: 1.4;
  color: var(--color-white);
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 40px 0;
}

.btn {
  height: 50px;
  padding: 0 20px;
  border: none;
  outline: none;
  border-radius: 30px;
  font-size: 14px;
  font-weight: 700;
  color: var(--color-white);
  background-color: var(--color-area);
  cursor: pointer;
  transition: .4s;
}
.btn:hover {
  background-color: var(--color-white-20);
}
.btn-primary {
  background-color: var(--color-primary);
  color: var(--color-white);
}
.btn-primary:hover {
  background-color: var(--color-hover);
}

.headline {
  margin-bottom: 40px;
  h1 {
    font-family: eight, sans-serif;
    font-size: 50px;
    line-height: 1;
    margin-bottom: 40px;
    text-align: center;
    span {
      font-size: 70px;
      color: var(--color-highlight);
    }
  }
  p {
    color: var(--color-white-50);
  }
}

.search {
  display: flex;
  gap: 10px;
  margin-bottom: 30px;
  input {
    flex-grow: 1;
    height: 50px;
    padding: 0 20px;
    border: none;
    border-bottom: 1px solid var(--color-white-50);
    outline: none;
    font-size: 14px;
    color: var(--color-white);
    background-color: rgba(0,0,0,0);
  }
  input::placeholder {
    color: var(--color-white-30);
  }
  .btn {
    flex-grow: 1;
    max-width: 150px;
  }
}

.movie-List {
  padding: 20px;
  border-radius: 4px;
  .movies {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    .movie {
      --width: 200px;
      width: var(--width);
      height: calc(var(--width) * 3 / 2);
      border-radius: 4px;
      background-color: var(--color-black);
      background-size: cover;
      overflow: hidden;
      .info {
        box-sizing: border-box;
        width: 100%;
        padding: 14px;
        font-size: 14px;
        text-align: center;
        position: absolute;
        left: 0;
        bottom: 0;
        background-color: rgba(0, 60, 255, 0.4);
        backdrop-filter: blur(10px);
        .year {
          color: var(--color-white);
        }
        .title {
          color: var(--color-white);
        }
      }
    }
  }
}
